.add-categories-screen {
    font-family: Arial, sans-serif;
  }
  
  .app-header {
    text-align: center;
  }
  
  .search-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
  }
  
  .search-container input {
    padding: 5px;
    font-size: 16px;
    border: 2px solid #ccc;
    border-radius: 5px;
    margin-right: 10px;
  }
  
  .search-container button {
    padding: 5px 10px;
    font-size: 16px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .categories-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 20px;
  }
  
  .category-item {
    background-color: #f2f2f2;
    border-radius: 20px;
    padding: 10px 20px;
    margin: 5px;
    display: flex;
    align-items: center;
  }
  
  .category-item label {
    margin-left: 5px;
    color: black
  }
  
  .navigation-buttons {
    position: fixed;
    bottom: 20px;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
  }
  
  